<template>
  <a-modal
    id="addUserModal"
    :title="isEdit ? '编辑成员' : '添加成员'"
    v-model="visible"
    width="800px"
    :footer="null"
  >
    <a-form class="kfc" :form="form">
      <a-row>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="昵称"
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'nick',
                { rules: [{ required: true, message: '请输入昵称' }] }
              ]"
              placeholder="请输入昵称(最大长度20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="密码"
          >
            <a-input
              minlength="6"
              :type="isEdit ? 'password' : 'text'"
              v-decorator="['password', passwordRules]"
              placeholder="请输入密码(最小长度6)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="邮箱"
          >
            <a-input
              maxlength="200"
              v-decorator="['email', emailRules]"
              placeholder="请输入邮箱"
            />
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="手机号"
          >
            <a-input
              maxlength="11"
              v-decorator="['mobile', phoneRules]"
              placeholder="请输入手机号(11位)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="姓名"
          >
            <a-input
              maxlength="10"
              v-decorator="[
                'realName',
                { rules: [{ required: false, message: '请输入姓名' }] }
              ]"
              placeholder="请输入姓名(最大长度10)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="职位"
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'position',
                { rules: [{ required: false, message: '请输入职位' }] }
              ]"
              placeholder="请输入职位(最大长度20)"
            />
          </a-form-item>
        </a-col>
      </a-row>

      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="所属机构"
        >
          <a-tree-select
            key="arm"
            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
            v-decorator="[
              'orgId',
              { rules: [{ required: true, message: '请选择所属机构' }] }
            ]"
            :treeData="treeArch"
            placeholder="请选择所属机构"
            treeDefaultExpandAll
          ></a-tree-select>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="所在地"
        >
          <a-input-search
            disabled
            v-model="oldAddress"
            v-if="hideCascader"
            @search="hideCascader = false"
          >
            <a-button slot="enterButton">修改所在地</a-button>
          </a-input-search>

          <a-cascader
            v-if="!hideCascader"
            v-decorator="['cascader', cascaderRules]"
            :options="options"
            changeOnSelect
            :loadData="loadCascaderData"
            @change="onCascaderChange"
            placeholder="请选择所在地"
          />
        </a-form-item>

        <a-row class="ant-form-item" v-if="cascaderResult !== null">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-input
              v-model="details"
              placeholder="请输入详细地址(最大长度200)"
            />
          </a-col>
        </a-row>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="添加功能权限"
        >
          <a-tree-select
            v-decorator="[
              'permissionIds',
              { rules: [{ required: true, message: '请添加功能权限' }],
                initialValue: [17] }
            ]"
            style="width: 100%"
            :treeData="authModule"
            treeCheckable
            @change="onChangeAuth"
            :showCheckedStrategy="SHOW_PARENT"
            searchPlaceholder="请添加功能权限"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="账户状态"
        >
          <a-radio-group
            v-decorator="[
              'userStatus',
              { rules: [{ required: true, message: '请设置账户状态' }] }
            ]"
          >
            <a-radio :value="1">启用</a-radio>
            <a-radio :value="0">停用</a-radio>
          </a-radio-group>
        </a-form-item>
        <br />
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false">取消</a-button
            >&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button type="primary" @click="handleSubmit">{{
              isEdit ? "更新" : "添加"
            }}</a-button>
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import {
  phoneRules,
  emailRules,
  passwordRules,
  cascaderRules,
  userApi,
  apiGet,
  apiPost
} from "@/api/api";
import { TreeSelect } from "ant-design-vue";

const SHOW_PARENT = TreeSelect.SHOW_PARENT;
export default {
  props: ['pid'],
  data() {
    return {
      phoneRules,
      emailRules,
      passwordRules,
      cascaderRules,
      SHOW_PARENT,
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false,
      form: this.$form.createForm(this),
      record: null,
      uid: null, // 用户ID
      isEdit: false, // 编辑状态
      hideCascader: false, // 隐藏省市县
      options: [], // 省市县
      cascaderResult: null, // 省市县选择结果
      authModule: [], // 权限模块
      treeArch: [], // 组织架构
      details: null // 详细地址
    };
  },
  watch: {
    visible: function(value) {
      if (value === false) {
        this.uid = null;
        this.form.resetFields();
        this.isEdit = false;
        this.hideCascader = false; // 用户地址
        this.oldAddress = null; // 用户地址
      }
    }
  },
  methods: {
    onChangeAuth(value) {
      // console.log("onChange ", value);
      this.$nextTick(() => {
        if (value[0] !== 18) {
          this.form.setFieldsValue({
            permissionIds: value.concat([17])
          });
        }
      });
    },
    initForm(record, auth) {
      setTimeout(() => {
        (this.record = record), (this.uid = record.user_id);
        this.isEdit = true;
        this.hideCascader = true; // 用户地址
        this.oldAddress = record.user_address; // 用户地址
        this.form.setFieldsValue({
          realName: record.real_name, // 成员姓名
          userStatus: record.user_status, // 用户状态
          position: record.position, // 职位

          mobile: record.mobile, // 手机
          email: record.email, // 邮箱
          nick: record.nick, // 昵称
          password: record.password, // 密码
          orgId: `${record.org_name}-${record.org_id}`,
          permissionIds: auth.map(i => i.permission_id) // 成员所在架构
        });
      });
    },
    async api(url, params) {
      try {
        let res = await apiPost(url, params);
        // let res = await fn(params);
        this.$parent.updateUserList();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    //获取数据加密密钥
    async apiGetRSA(values) {
      try {
        let res = await apiGet(userApi.getRSA);
        // let res = await getRSA();
        this.$rsa.setPublicKey(res.data.public_key);

        let params = {
          userId: this.isEdit ? this.uid : undefined,
          parentUserId: this.pid, // 成员父级
          orgId: Number(values.orgId.split("-").pop()), // 成员所在架构
          realName: values.realName, // 成员姓名
          accountType: 0, // 成员账户类型
          mobile: values.mobile, // 成员所在架构
          email: values.email, // 成员所在架构
          nick: values.nick, // 成员所在架构
          password: undefined, // 成员所在架构
          permissionIds: values.permissionIds.join(","), // 成员所在架构
          userStatus: Number(values.userStatus), // 成员所在架构
          position: values.position, // 成员所在架构
          userAddress: this.cascaderResult
            ? `${this.cascaderResult.join("-")}-${this.details || ""}`
            : undefined // 所在地
        };

        if (this.isEdit) {
          if (values.password !== this.record.password) {
            let password = this.$rsa.encrypt(values.password);
            if (!password) return this.$message.error("数据加密失败，请重试！");
            params.password = password;
          }
          this.api(userApi.updateSubUser, params); // 编辑
        } else {
          let password = this.$rsa.encrypt(values.password);
          if (!password) return this.$message.error("数据加密失败，请重试！");
          params.password = password;
          this.api(userApi.addSubUser, params); // 新增
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    async loadCascaderData(selectedOptions) {
      try {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;

        let res = await apiPost(userApi.getCity, {
          postCode: targetOption.value || 0
        });

        if (res.valid) {
          targetOption.loading = false;
          targetOption.children = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: targetOption.deep + 1,
              isLeaf: targetOption.deep + 1 > 1
            };
          });

          this.options = [...this.options];
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    // 三级联动
    onCascaderChange(codeList, dataList) {
      if (dataList) {
        this.cascaderResult = dataList.map(i => i.label);
      } else {
        this.cascaderResult = null;
      }
    },
    // 提交表单
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) this.apiGetRSA(values);
      });
    }
  }
};
</script>
