<template>
  <div id="header-user">
    <div class="user">
      <p class="avater">
        <img src="../assets/images/avatar.png" alt="头像" />
      </p>
      <p class="username">管理员</p>
      <p class="icon">
        <!-- <a-icon type="down" /> -->
        <i class="el-icon-arrow-down"></i>
      </p>
      <div class="menu">
        <template v-if="roler !== 's'">
          <!-- 管理员 -->
          <a href="#" @click="goHome">管理员控制台</a>
          <router-link
            to="/backend"
            :class="{ active: $route.path === '/backend' }"
            >后台管理</router-link
          >
          <a href="#" @click="exit">退出系统</a>
        </template>

        <template v-if="roler === 's'">
          <!-- 子用户 -->
          <a href="#" @click="goHome">控制台</a>
          <router-link
            to="/bigScreen"
            :class="{ active: $route.path === '/bigScreen' }"
            >大屏数据</router-link
          >
          <a href="#" @click="exit">退出系统</a>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import { apiPost } from "@/api/api";
export default {
  data() {
    return {
      roler: localStorage.getItem("roler")
    };
  },
  methods: {
    goHome() {
      localStorage.clear();
      window.location.replace(window.MYHOST);
    },
    // 获取架构
    async exit() {
      try {
        let res = await apiPost("/api/v1/logout.php", {
          'jsessionId': localStorage.getItem('cookie') || undefined
        });
        localStorage.clear();
        window.location.replace(window.MYHOST);

        window.console.log(res);
      } catch (error) {
        window.console.log(error);
      }
    }
  }
};
</script>
