<template>
  <a-modal
    id="addUserModal"
    :title="defaultId === null ? '添加机构' : '编辑机构'"
    v-model="visible"
    @ok="visible = false"
    :footer="null"
  >
    <a-form class="kfc" :form="form" @submit="handleSubmit">
      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          :label="defaultId === null ? '机构名称' : '重新命名'"
        >
          <a-input
            maxlength="20"
            v-decorator="[
              'name',
              {
                initialValue: defaultTitle,
                rules: [{ max: 20, required: true, message: '请输入机构名称' }]
              }
            ]"
            placeholder="请输入机构名称(最大长度20)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          :label="defaultId === null ? '上级机构' : '当前机构'"
        >
          <a-tree-select
            showSearch
            :disabled="defaultValue !== null"
            key="arm"
            v-decorator="[
              'id',
              {
                initialValue: defaultValue,
                rules: [{ required: true, message: '请输入机构名称' }]
              }
            ]"
            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
            :treeData="treeData"
            placeholder="请选择上级机构"
            treeDefaultExpandAll
          ></a-tree-select>
        </a-form-item>

        <br />
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false">取消</a-button
            >&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button
              type="primary"
              v-if="defaultId === null"
              @click.prevent="handleSubmit('add')"
              >添加</a-button
            >
            <a-button
              type="primary"
              v-else
              @click.prevent="handleSubmit('edit')"
              >更新</a-button
            >
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import { userApi, apiPost } from "@/api/api";
export default {
  props: ['uid'],
  data() {
    return {
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false,
      form: this.$form.createForm(this),
      treeData: [], // 可选架构
      defaultValue: null, // 当前机构值
      defaultTitle: null, // 当前机构名称
      defaultId: null, // 当前ID
      pid: 0 // 父ID
    };
  },
  methods: {
    async api(url, params) {
      try {
        let res = await apiPost(url, params);
        this.$parent.apiGetArch();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    handleSubmit(type) {
      this.form.validateFields((err, values) => {
        console.log(this)
        if (!err) {
          // console.log("Received values of form: ", values);
          if (type == "edit") {
            this.api(userApi.updOrganization, {
              companyId: this.uid,
              orgParentId: this.pid,
              orgName: values.name,
              orgId: Number(values.id.split("-").pop())
            });
          } else if (type == "add") {
            this.api(userApi.addOrganization, {
              companyId: this.uid,
              orgName: values.name,
              orgParentId: Number(values.id.split("-").pop())
            });
          }
        }
      });
    }
  }
};
</script>

