<template>
  <div id="backend">
    <div class="header">
      <p>用户管理</p>
      <HeaderUser class="opts" />
    </div>
    <a-locale-provider :locale="zhCN">
      <div class="main">
        <div class="arch-row kfc">
          <div class="arch-left">
            <a-form layout="inline">
              <a-form-item>
                <span class="arch-title">组织架构</span>
              </a-form-item>
              <a-form-item class="pull-right">
                <a-button icon="plus" @click="showModalARM(null, null, 'add')"
                  >添加架构</a-button
                >
              </a-form-item>
            </a-form>
          </div>

          <div class="arch-right">
            <a-form layout="inline" style="border-left:1px solid #efefef">
              <a-form-item style="margin-left:16px;">
                <a-button class="bgGray" @click="showModalAUM"
                  >添加成员用户</a-button
                >
              </a-form-item>
              <a-form-item>
                <a-button
                  class="bgGray"
                  :loading="activeUserBtnLoading"
                  :disabled="selectedRowKeys.length === 0"
                  @click="
                    apiUpdateUserStatus('enable', selectedRowKeys.join(','))
                  "
                  >启用账户</a-button
                >
              </a-form-item>
              <a-form-item>
                <a-button
                  class="bgGray"
                  :loading="unactiveUserBtnLoading"
                  :disabled="selectedRowKeys.length === 0"
                  @click="
                    apiUpdateUserStatus('pending', selectedRowKeys.join(','))
                  "
                  >停用账户</a-button
                >
              </a-form-item>

              <a-form-item class="pull-right" style="margin:0">
                <a-button type="primary" @click="onTableSearch">搜索</a-button>
              </a-form-item>
              <a-form-item class="pull-right">
                <a-input
                  allowClear
                  v-model="table.searchStr"
                  placeholder="请输入昵称或手机号"
                />
              </a-form-item>
              <a-form-item class="pull-right">
                <a-select
                  v-model="userStatus"
                  placeholder="账户状态"
                  style="width: 120px"
                  allowClear
                >
                  <a-select-option value="0">未审核</a-select-option>
                  <a-select-option value="1">已审核</a-select-option>
                  <a-select-option value="2">已停用</a-select-option>
                </a-select>
              </a-form-item>
              <a-form-item class="pull-right">
                <a-select
                  v-model="userOrg"
                  placeholder="部门筛选"
                  style="width: 120px"
                  allowClear
                >
                  <a-select-option
                    v-for="(item, index) in orignArch"
                    :key="index"
                    :value="item.org_name"
                  >
                    {{ item.org_name }}</a-select-option
                  >
                </a-select>
              </a-form-item>
            </a-form>
          </div>
        </div>
        <div class="arch-row kfc">
          <div class="arch-left">
            <a-spin :spinning="archSpinning">
              <div class="spin-content">
                <a-tree
                  showLine
                  defaultExpandAll
                  v-if="treeArch.length"
                  class="arch-tree"
                  @select="onTreeSelect"
                  :treeData="treeArch"
                  :scroll="{ x: 1300 }"
                >
                  <template
                    slot="title"
                    slot-scope="{
                      title,
                      key,
                      pid,
                      deep,
                      isAddChild,
                      isRemove,
                      isEdit
                    }"
                  >
                    <span :title="title">{{ title }}</span>
                    <a-popover title="操作" placement="right" trigger="hover">
                      <template slot="content">
                        <p
                          v-if="isAddChild"
                          class="treeInnerBtn"
                          @click="showModalARM(title, key, 'add')"
                        >
                          新建下级部门
                        </p>
                        <p
                          class="treeInnerBtn"
                          v-if="isEdit"
                          @click="showModalARM(title, key, 'edit', pid)"
                        >
                          编辑
                        </p>
                        <p
                          class="treeInnerBtn"
                          v-if="isRemove"
                          @click="apiRemoveArch(key)"
                        >
                          删除
                        </p>
                      </template>
                      <a-icon
                        class="pull-right treeSettingBtn"
                        type="setting"
                      />
                    </a-popover>
                  </template>
                </a-tree>
              </div>
            </a-spin>
          </div>
          <div class="arch-right arch-Table">
            <!-- 表格 -->
            <a-table
              bordered
              :loading="table.loading"
              :pagination="false"
              :columns="columns"
              :dataSource="table.list"
              :rowSelection="rowSelection"
            >
              <div slot="user_status" slot-scope="text, record">
                <span v-if="record.user_status === 0">未审核</span>
                <span v-if="record.user_status === 1">已审核</span>
                <span v-if="record.user_status === 2">已停用</span>
                <span v-if="record.user_status === 3">已拒绝</span>
              </div>
              <div slot="last_login_time" slot-scope="text, record">
                {{
                  record.last_login_time &&
                    fmtDate("Y-M-D hh:mm:ss", record.last_login_time)
                }}
              </div>
              <div slot="operation" slot-scope="text, record">
                <a-icon
                  v-if="record.user_status === 1"
                  class="iconStyle"
                  type="edit"
                  @click="showModalAUM(record)"
                  title="编辑"
                />

                <a-icon
                  v-if="record.user_status === 0 || record.user_status === 3"
                  class="iconStyle"
                  type="safety-certificate"
                  title="授权"
                  @click="apiUpdateUserStatus('enable', record.user_id)"
                />
                <a-icon
                  v-if="record.user_status === 0"
                  class="iconStyle"
                  type="stop"
                  @click="apiUpdateUserStatus('refuse', record.user_id)"
                  title="拒绝"
                />

                <a-button-group
                  v-if="record.user_status !== 0 && record.user_status !== 3"
                >
                  <a-switch
                    defaultChecked
                    title="激活状态"
                    checkedChildren="开"
                    unCheckedChildren="关"
                    :checked="record.user_status === 1"
                    @click="
                      checked =>
                        apiUpdateUserStatus(
                          checked ? 'enable' : 'pending',
                          record.user_id
                        )
                    "
                  />
                </a-button-group>
              </div>
            </a-table>

            <div class="arch-pagination">
              <a-pagination
                showSizeChanger
                showQuickJumper
                v-model="table.index"
                :total="table.total"
                :pageSize.sync="table.pageSize"
                @change="onPageIndexChange"
                @showSizeChange="onPageSizeChange"
                :showTotal="
                  (total, range) =>
                    `共 ${total} 条记录  第 ${this.table.index}/${Math.ceil(
                      this.table.total / this.table.pageSize
                    )} 页`
                "
              ></a-pagination>
            </div>
          </div>
        </div>
      </div>
    </a-locale-provider>
    <!-- 模态框 -->
    <addUserChildModal ref="aum" :pid="uid" />
    <addArchModal ref="arm" :uid="uid" />
  </div>
</template>

<script>
import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
import HeaderUser from "@/components/headerUser.vue";
import addUserChildModal from "@/components/addUserChildModal.vue";
import addArchModal from "@/components/addArchModal.vue";
import {
  userApi,
  apiPost,
  fmtAuth,
  fmtArch,
  userChildColumns
} from "@/api/api";

import { fmtDate } from "@/utils/utils";
export default {
  components: {
    HeaderUser,
    addArchModal,
    addUserChildModal
  },
  data() {
    return {
      zhCN,
      uid: localStorage.getItem("uid"),
      columns: userChildColumns,
      selectedRowKeys: [], // 多选
      orignArch: [], // 数据库里的架构
      treeArch: [], // 本地架构
      activeUserBtnLoading: false, // 启用账户
      unactiveUserBtnLoading: false, // 停用账户
      archSpinning: false,
      userOrg: undefined, // string
      userStatus: undefined, // 搜索-账户状态 enable:正常状态，pending: 待激活状态，disable: 禁用状态, refuse: 拒绝状态
      table: {
        searchStr: null, // 搜索-请输入用户昵称或手机号
        list: [],
        total: 50,
        index: 1,
        size: 10,
        loading: false,
        pageSize: 10
      }
    };
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onTableSelectChange,
        onSelection: this.onSelection
        // getCheckboxProps: record => ({
        //   props: {
        //     disabled: record.user_status === 0
        //   }
        // })
      };
    }
  },
  created() {
    this.updateUserList();
    this.apiGetArch();
  },
  methods: {
    fmtDate,
    // 表哥多选
    onTableSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRows.map(i => i.user_id);
      // window.console.log(this.selectedRowKeys);
    },
    // 架构模态框 => 添加 || 编辑
    showModalARM(title = null, key = null, type, pid) {
      this.$nextTick(() => {
        console.log(this.$refs);
        this.$refs.arm.form.resetFields();
        this.$refs.arm.defaultId = null;
        this.$refs.arm.treeData = [];
        this.$refs.arm.defaultValue = null;
        this.$refs.arm.defaultTitle = null;

        if (type === "edit") {
          // 显示全部节点，当前节点 => 重新命名
          this.$refs.arm.treeData = this.treeArch;
          this.$refs.arm.defaultValue = title + "-" + key;
          this.$refs.arm.defaultId = Number(key);
          this.$refs.arm.defaultTitle = title;
          this.$refs.arm.pid = pid;
        }

        if (type === "add") {
          this.$refs.arm.treeData = fmtArch(this.orignArch, 4);
          if (title && key) this.$refs.arm.defaultValue = title + "-" + key;
        }

        this.$refs.arm.visible = true;
      });
    },
    // 表格搜索
    onTableSearch() {
      this.table.index = 1;
      this.updateUserList();
    },
    // 获取架构
    async apiGetArch() {
      this.archSpinning = true;
      try {
        let res = await apiPost(userApi.findAllByCompanyId, {
          companyId: this.uid
        });

        if (!res.valid) return this.$message.error(res.msg);
        this.orignArch = res.data;
        this.treeArch = fmtArch(res.data, 5);
      } catch (error) {
        window.console.log(error);
      }
      this.archSpinning = false;
    },
    // 删除架构
    async apiRemoveArch(key) {
      this.archSpinning = true;
      try {
        let res = await apiPost(userApi.delOrganization, {
          orgId: Number(key)
        });
        if (!res.valid) return this.$message.error(res.msg);
        // 成功，刷新树，关闭模态框
        this.apiGetArch();
        this.$refs.arm.visible = false;
      } catch (error) {
        window.console.log(error);
      }
      this.archSpinning = false;
    },
    // 表哥更新
    async updateUserList() {
      this.table.loading = true;
      try {
        let res = await apiPost(userApi.getAllSubUsers, {
          // let res = await getAllChild({
          pageNum: this.table.index,
          pageSize: this.table.pageSize,
          parentUserId: this.uid,
          keyword: this.table.searchStr || undefined,
          userStatus: this.userStatus ? Number(this.userStatus) : undefined,
          orgName: this.userOrg || undefined
        });

        this.table.total = res.data.recordCount;
        this.table.list = res.data.result.map(row => {
          row["key"] = row.user_id;
          row["rowKey"] = row.user_id;
          return row;
        });
        setTimeout(() => (this.selectedRowKeys = []));
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    },
    // 修改用户状态
    async apiUpdateUserStatus(status, userIds) {
      this.table.loading = true;
      try {
        let res = await apiPost(userApi.updateUserStatus, {
          // let res = await updateUserStatus({
          status,
          userIds
        });
        this.updateUserList();
        this.$message.success(res.msg);
      } catch (error) {
        window.console.log(error);
      }

      this.table.loading = false;
      this.activeUserBtnLoading = false;
      this.unactiveUserBtnLoading = false;
    },
    // 表格当前页码
    onPageIndexChange(page, pageSize) {
      window.console.log(page, pageSize);
      this.table.index = page;
      this.table.pageSize = pageSize;
      this.updateUserList();
    },
    // 表格每页数量
    onPageSizeChange(current, pageSize) {
      window.console.log(current, pageSize);
      this.table.index = 1;
      this.table.pageSize = pageSize;
      this.updateUserList();
    },
    // 树节点选择
    onTreeSelect(selectedKeys, info) {
      window.console.log("selected", selectedKeys, info);
    },
    async showModalAUM(record) {
      this.table.loading = true;
      try {
        // 获取全部权限
        let auth = await apiPost(userApi.getLingXePermission);
        if (auth.valid === false) return this.$message.error(auth.msg);
        // 获取父级权限
        let auth2 = await apiPost(userApi.getLingXePermissionByUserId, {
          userId: this.uid
        });
        if (auth2.valid === false) return this.$message.error(auth2.msg);

        // 获取省份
        let city = await apiPost(userApi.getCity, {
          postCode: 0
        });
        if (city.valid === false) return this.$message.error(city.msg);

        // 数据回填
        this.$refs.aum.authModule = fmtAuth(auth.data, auth2.data);
        this.$refs.aum.treeArch = this.treeArch;
        this.$refs.aum.options = city.data.map(item => {
          return {
            value: item.post_code,
            label: item.city_name,
            deep: 0,
            isLeaf: false
          };
        });
        // 编辑
        if (typeof record.user_id !== "undefined") {
          // 获取孩子
          let child = await apiPost(userApi.getSubUserByUserId, {
            //   let child = await getChildUser({
            userId: record.user_id,
            parentUserId: this.uid
          });
          if (child.valid === false) return this.$message.error(child.msg);
          // 获取孩子权限
          let cAuth = await apiPost(userApi.getLingXePermissionByUserId, {
            userId: record.user_id
          });
          //   let cAuth = await getUserAuth({ userId: record.user_id });
          if (cAuth.valid === false) return this.$message.error(cAuth.msg);

          this.$refs.aum.initForm(child.data, cAuth.data);
          this.$refs.aum.visible = true;
        } else this.$refs.aum.visible = true;
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    },
    async apiUpdateSubUser(params) {
      this.table.loading = true;
      try {
        let res = await apiPost(userApi.updateSubUser, params);
        // let res = await updateSubUser(params);

        this.updateUserList();
        this.$message.success(res.msg);
        this.$refs.aum.visible = false;
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    },
    async apiAddSubUser(params) {
      this.table.loading = true;
      try {
        let res = await apiPost(userApi.addSubUser, params);
        // let res = await addSubUser(params);

        this.updateUserList();
        this.$message.success(res.msg);
        this.$refs.aum.visible = false;
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/style/mixin.scss";
#backend {
  background: #f1f4fa;
  height: 100%;
  .opts {
    color: white;
  }
  .header {
    height: 80px;
    background: $main;
    position: relative;
    line-height: 80px;
    > p {
      height: 80px;
      line-height: 80px;
      text-align: center;
      letter-spacing: 2px;
      color: white;
      font-size: 24px;
    }
  }
  .main {
    height: calc(100% - 80px);
    overflow-x: hidden;
    overflow-y: auto;

    .arch-pagination {
      text-align: right;
      padding: 30px 0 30px 0;
    }
    .arch-row {
      margin: 0 20px;
      padding: 20px;
      background: #fff;
      &:first-child {
        border-bottom: 1px solid #d9d9d9;
        padding-bottom: 15px;
        margin-top: 20px;
      }
    }
    .arch-title {
      font-size: 18px;
      color: #666666;
    }
    .arch-right {
      float: left;
      width: calc(100% - 240px);
    }
    .arch-Table {
      padding: 20px 0 0 20px;
      border-left: 1px solid #d9d9d9;
    }
    .arch-left {
      float: left;
      width: 240px;
      border-right: 1px solid #d9d9d9;
      margin-right: -1px;
    }
    .arch-tree {
      margin-right: 16px;
      position: relative;
    }
    .addArch {
      font-size: 16px;
      color: $blue;
      text-align: center;
      cursor: pointer;
      padding: 4px;
      border-radius: 4px;
      &:hover {
        background: $bgColor;
      }
    }
  }
}
</style>


